import React, { useMemo } from 'react'
import { Modal } from 'react-bootstrap'
import { useLocation } from 'react-router-dom'
import styled from 'styled-components'
import {
  Button,
  Card,
  PaddedContent,
  Row,
  useDeviceDetect,
  useIdentity,
  useQueryParam,
} from '@aserto/console-common'

import sadAxolotl from '../../assets/axolotl.svg'

const CardModalContainer = styled(Modal)`
  overflow: hidden;
  border-radius: 20px;
  .modal-content {
    border-radius: 20px;
  }
`

const CardContent = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
  height: 100%;
  strong {
    color: ${({ theme }) => theme.grey100};
  }
  img {
    align-self: baseline;
    margin-right: 14px;
    width: 120px;
  }
`

const ButtonContainer = styled.div`
  button {
    float: right;
  }
`

const MessageText = styled.div`
  margin-top: 15px;
  margin-bottom: 10px;
  a {
    color: ${({ theme }) => theme.lochivarAccent4};
  }
`

const getErrorObject = (key: string): { message: string | React.ReactElement; title: string } => {
  const obj: Record<string, { message: React.ReactElement; title: string }> = {
    'not-allowed': {
      message: (
        <>
          This may be a temporary error. Please try reloading this page, and signing out and back
          in. If the error persists after trying these steps, contact{' '}
          <a href="mailto:support@aserto.com?subject=Support%20needed">support@aserto.com</a>.
        </>
      ),
      title: "You're not allowed to see this invitation.",
    },
  }

  return obj[key]
}

const Error = () => {
  const queryParams = useQueryParam(useLocation().search)
  const { logout } = useIdentity()
  const { isMobile } = useDeviceDetect()
  const key = queryParams.get('key') as 'no-member'

  const errorObj = useMemo(() => {
    return getErrorObject(key)
  }, [key])

  return (
    <>
      <PaddedContent>
        <CardModalContainer centered data-testid="error-modal" show>
          <Card
            body={
              <CardContent>
                <Row $flex>
                  {!isMobile && <img src={sadAxolotl} />}
                  <div>
                    <strong>{errorObj?.title}</strong>
                    <MessageText>{errorObj?.message}</MessageText>
                  </div>
                </Row>
                <ButtonContainer>
                  <Button variant="secondary" onClick={logout}>
                    Sign out
                  </Button>
                </ButtonContainer>
              </CardContent>
            }
            height={isMobile ? 340 : 323}
            style={{
              width: '100%',
            }}
            title="Oh, snap!"
            variant="danger"
          />
        </CardModalContainer>
      </PaddedContent>
    </>
  )
}

export default Error
